import React, { useEffect } from "react";
import { Container } from "react-bootstrap";
// needed styles
import styles from "./index.module.scss";
// needed components
// assets
import HeroSlider from "./HeroSlider/HeroSlider";
import WhiteCard from "components/shared/WhiteCard/WhiteCard";
import ProgramDetailsTable from "./ProgramDetailsTable/ProgramDetailsTable";
import ProgramDetails from "./ProgramDetails/ProgramDetails";
import PriceTable from "./PriceTable/PriceTable";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";

import { getSingleHajjTrip, getSingleUmraTrip } from "store/trips/slice";

const SingleTripPage = () => {
  const { tripType, tripId } = useParams();
  const dispatch = useDispatch();
  useEffect(() => {
    if (tripType === "hajj") return dispatch(getSingleHajjTrip(tripId));
    if (tripType === "umra") return dispatch(getSingleUmraTrip(tripId));
  }, [dispatch, tripType, tripId]);

  const { singleTrip, loading } = useSelector((state) => state.trips) || {};

  const {
    title_ar,
    title_en,
    start_flight_date,
    required_documents_en,
    required_documents_ar,
    airline_ar,
    airline_en,
    company_id,
    desc_ar,
    desc_en,
    duration_ar,
    duration_en,
    end_flight_date,
    hotel_ar,
    hotel_en,
    itinerary_title_ar,
    itinerary_title_en,
    madina_night,
    means_of_travel_ar,
    means_of_travel_en,
    mecca_night,
    pictures,
    price,
    price_includes_ar,
    price_includes_en,
    price_not_includes_ar,
    price_not_includes_en,
  } = singleTrip || {};
  console.log(singleTrip, "singleTrip");
  return (
    <Container fluid className={styles["wrapper"]}>
      <Container className={styles["container"]}>
        <h2 className={styles["slider-header"]}> {title_ar}</h2>
        <h3 className={styles["slider-sub-header"]}> {itinerary_title_ar}</h3>
        <HeroSlider data={pictures} />
        <WhiteCard>{desc_ar}</WhiteCard>
        <ProgramDetailsTable singleTrip={singleTrip} />
        <ProgramDetails
          rightIcon={true}
          header="السعر يشمل"
          data={price_includes_ar}
        />
        <ProgramDetails
          wrongIcon={true}
          header="السعر لايشمل"
          data={price_not_includes_ar}
        />
        <ProgramDetails
          rightIcon={true}
          header="الاوراق والمستندات المطلوبه"
          data={required_documents_ar}
        />
        <PriceTable singleTrip={singleTrip} />
        {/* <ProgramDetails
          wrongIcon={true}
          header="ملاحظات هامه"
          data={singleTrip}
        /> */}
      </Container>
    </Container>
  );
};

export default SingleTripPage;
